@import '~node_modules/modern-normalize/modern-normalize.css';
h1, h2, h3, h4, h5, h6 {
    margin: 0;
    padding: 0;
}
ul, li {
    padding: 0;
    margin: 0;
    text-decoration: none;
    list-style: none;
}
a {
    color: inherit;
    text-decoration: none;
}
img {
    display: block;

}
body {
    padding: 0;
    font-family: 'Roboto', sans-serif;
}
.container{
    width: 1200px;
    margin: 0 auto;
    padding: 0 15px;
}